<template>
  <!---->
  <div class="container">
    <el-carousel trigger="click" height="500px">
      <el-carousel-item v-for="(item, index) in items" :key="index">
        <div
          class="image-container"
          :style="{ backgroundImage: 'url(' + item.image + ')' }"
          @click="handleClick(item)"
        ></div>
        <h3 class="small">{{ item.title }}<br />{{ item.content }}</h3>
      </el-carousel-item>
    </el-carousel>

    <!--1-->

    <div class="introduce1">
      <div class="right" data-aos="zoom-in" data-aos-duration="1500">
        <img src="../../assets/beer.png" class="beer" />
      </div>
      <div class="left">
        <h1> </h1>
        <div>
          <p>
            
          </p>
          <p>
            
          </p>
          <p>
            
          </p>
          <p>
            
          </p>
        </div>
        <button @click="ToAbout"></button>
      </div>
    </div>

    <!--2-->

    <div class="introduce2">
      <div class="left" data-aos="zoom-in" data-aos-duration="1500">
        <img src="../../assets/winery.png" class="winery" />
      </div>
      <div class="right">
        <h1></h1>
        <div>
          <p>
            
          </p>
          <p>
            
          </p>
        </div>
        <button @click="ToBrewing"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [
        {
          name: "poster1",
          image: require("../../assets/poster1.jpg"),
          title: "",
          content: "!",
        },
        {
          name: "poster2",
          image: require("../../assets/poster2.jpg"),
          title: "",
          content: "!",
        },
      ],
    };
  },
  methods: {
    //
    ToAbout() {
      this.$router.push({
        path: "/about",
      });
    },
    ToBrewing() {
      this.$router.push({
        path: "/brewing",
      });
    },
    //
    handleClick(item) {
      if (item.name === "poster1") {
        this.poster1Method();
      } else if (item.name === "poster2") {
        this.poster2Method();
      }
    },
    //
    poster1Method() {
      this.$router.push({
        path: "/news/activity",
      });
    },
    poster2Method() {
      this.$router.push({
        path: "/news/new-product",
      });
    },
  },
};
</script>

<style scoped>
.el-carousel__item {
  background-color: #99a9bf;
}

.small {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  text-align: center;
}
.image-container {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  position: relative;
}
.image-container:hover {
  cursor: pointer;
}
.left,
.right {
  width: 50%;
}
img {
  width: 100%;
}
.introduce1,
.introduce2 {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  line-height: 1.9;
  padding: 0 50px;
  box-sizing: border-box;
}
.introduce1 {
  background-color: rgba(69, 53, 38, 0.5);
}
h1 {
  color: rgb(0, 0, 110);
}
p {
  font-size: 18px;
  font-weight: bold;
}
.introduce2 {
  background-color: rgba(252, 212, 94, 0.3);
}
button {
  width: 200px;
  height: 50px;
  font-size: 20px;
  margin: 20px 0;
  font-weight: bolder;
  background-color: rgba(239, 239, 36, 0.311);
  border-radius: 10px;
}

button:hover {
  cursor: pointer;
  background-color: rgba(239, 239, 36, 0.622);
  color: rgb(0, 0, 110);
}
@media screen and (max-width: 1024px) {
  .right,
  .left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
  }
  .introduce1,
  .introduce2 {
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
  img {
    width: 50%;
  }
}
@media screen and (max-width: 767px) {
  .right,
  .left {
    width: 100%;
  }
  h1 {
    font-size: 25px;
  }
  img {
    width: 80%;
  }
  .el-carousel {
    height: 400px;
    overflow-y: hidden;
  }
  .small {
    top: 30%;
    font-size: 40px;
  }
  ::v-deep .el-carousel__arrow--left,
  ::v-deep .el-carousel__arrow--right {
    top: 200px;
  }
}
@media screen and (max-width: 414px) {
